import classNames from "classnames";
import { ReactNode, SyntheticEvent } from "react";
import ExpandLessIcon from "@digitalservicebund/icons/ExpandLess";
import ExpandMoreIcon from "@digitalservicebund/icons/ExpandMore";
import { PayoutInformation } from "./PayoutInformation";
import { PayoutAmoutForVariant } from "./types";

type Props = {
  readonly summaryTitle: string;
  readonly summaryClassName?: string;
  readonly monthsAvailable: number;
  readonly payoutAmounts: PayoutAmoutForVariant[];
  readonly children: ReactNode;
};

export function DetailsElterngeldvariante({
  summaryTitle,
  summaryClassName,
  monthsAvailable,
  payoutAmounts,
  children,
}: Props): ReactNode {
  const isSingleParent = payoutAmounts.length === 1;

  return (
    <details
      className="group overflow-hidden rounded bg-grey"
      name="Elterngeldvariante"
      onToggle={closeOtherOpenElementsOfSameGroup}
    >
      <summary
        className={classNames(
          "flex justify-between list-none items-center cursor-pointer px-24 py-16",
          summaryClassName,
        )}
      >
        <div className="flex flex-wrap gap-y-10">
          <div className="flex basis-full flex-wrap items-center gap-x-8">
            <strong className="text-24">{summaryTitle}</strong>(
            {monthsAvailable} Monate verfgbar)
          </div>

          <div className="flex flex-wrap gap-x-24 gap-y-8">
            {payoutAmounts.map(({ name, amount }) => (
              <PayoutInformation
                key={name}
                name={isSingleParent ? undefined : name}
                amount={amount}
              />
            ))}
          </div>
        </div>

        <ExpandMoreIcon className="min-h-40 min-w-40 group-open:hidden" />
        <ExpandLessIcon className="hidden min-h-40 min-w-40 group-open:block" />
      </summary>

      <div className="bg-off-white p-32 pb-56">{children}</div>
    </details>
  );
}

function closeOtherOpenElementsOfSameGroup(
  event: SyntheticEvent<HTMLDetailsElement, ToggleEvent>,
): void {
  const targetElement = event.target as Element;
  const isDetailsElement = targetElement.tagName.toLowerCase() === "details";
  const nameAttributeValue = targetElement.getAttribute("name");
  const hasNameAttribute = nameAttributeValue !== null;
  const isPartOfDetailsGroup = isDetailsElement && hasNameAttribute;
  const hasOpened = targetElement.getAttribute("open") !== null;

  if (isPartOfDetailsGroup && hasOpened) {
    const openElementsOfGroup = Array.from(
      document.querySelectorAll(`details[name=${nameAttributeValue}][open]`),
    );

    const elementsToClose = openElementsOfGroup.filter(
      (element) => element !== targetElement,
    );

    elementsToClose.forEach((element) => element.removeAttribute("open"));
  }
}
